.App
{
    width: 410px;
    font-family: Arial, sans-serif;
}

#playerInfo,.scoreCard
{
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
}

.clickButtons
{
    display: inline-block;
    margin-left: 50px;
}

.btn-points
{
    margin-left: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: inline-block;
    cursor: pointer;
}

#playerName
{
    margin-bottom: 0;
    background-color: rgba(0,0,0,.03);
    border-bottom: 1px solid rgba(0,0,0,.125);
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    margin-top: 0px;
}

#playerPoints
{
    margin-bottom: .5rem;
    text-align: center;
    display: inline-block;
    border: 3px solid #000;
    width: 30px;
    height: 40px;
    margin-top: 10px;
}

table, th, td {
    border: 3px solid grey;
    border-collapse: collapse;
    text-align: center;
}

th,td{
    padding: 10px;
}

table{
    margin-left: 60px;
}
.playerScores
{
    text-align: center;
}

.createBoard
{
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 130px;
}

.resetBoard
{
    margin-left: 75px;
    display: inline-block;
    margin-top: 10px;
    margin-bottom: 10px;
}

.medalOne
{
    color: #FFD700;
    cursor: pointer;
}

.medalThree
{
    color: #cd7f32;
    cursor: pointer;
}

.medalTwo
{
    color: #C0C0C0;
    cursor: pointer;
}

.rankOne
{
    text-align: center;
    margin-top: 7px;
}

.rankTwo
{
    display: inline-block;
    text-align: center;
}

.rankThree
{
    display: inline-block;
    text-align: center;
    margin-left: 150px;
}

.ranking
{
    display: inline-block;
    text-align: center;
    margin-left: 75px;
}

.goHome
{
    display: inline-block;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 110px;
}

.addPlayer
{
    margin-left: 10px;
}

h1
{
    display: block;
    margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

.btn
{
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    border: 1px solid transparent;
}

.changeBy
{
    margin-left: 10px;
    width: 50px;
    display: inline-block;
}

.btn-add-player,.btn-search-player
{
    margin-left: 20px;
    margin-bottom: 10px;
    display: inline-block;
    cursor: pointer;
}

.newPlayer{
    margin-left: 5px;
    color: #00C1D4;
}

.playButton,.pauseButton,.resumeButton,.redoButton
{
    font-size: 3em;
}

.userPlus
{
    color: #00C1D4;
}

.searchPlayer{
    margin-right: 3px;
    display: inline-block;
}

.btn-search-player{
    margin-top: 10px;
}

.player-name
{
    margin-left: 20px;
}

.iconTrash
{
    margin-left: 50px;
    display: inline-block;
    cursor: pointer;
    color: #F8485E;
}
.iconClock
{
    display: inline-block;
    margin-left: 10px;
    cursor: pointer;
    color: #F8485E;
}

#playerCard
{
    display: inline-block;
}

.timer{
    width: 100%;
    display: flex;
    height: 12%;
    justify-content: center;
    align-items: center;
}
  
.digits{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 3rem;
}

.btn-grp
{
    margin-left: 100px;
    display: inline-block;
}

.btn-one
{
    display: inline-block;
    cursor: pointer;
}

.btn-two
{
    margin-left: 100px;
    display: inline-block;
    cursor: pointer;
}
.btn-start
{
    margin-left: 170px;
    display: inline-block;
    cursor:pointer;
}

.btn-time
{
    margin-left: 85px;
}

.btn-close
{
    margin-left: 90px;
}

.btn-time,.btn-close
{
    margin-top: 10px;
    margin-bottom: 10px;
}

.currentSavedTime
{
    margin-left: 100px;
    margin-top: 20px;
    margin-bottom: 10px;
}
.profileImg
{
    border-radius: 50%;
    margin-right: 10px;
    width: 40px;
    height: 40px;
}